(function() {
  'use strict';

  angular
    .module('gerbsDawg', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ui.router', 'ui.bootstrap', 'toastr', 'firebase', 'cfp.hotkeys']);

})();

(function() {
  'use strict';

  angular
    .module('gerbsDawg')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        values: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 80,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: false,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.values, function(value) {
        typist.type(value);
      });

      // watcher = scope.$watch('vm.contributors', function() {
      //   angular.forEach(vm.contributors, function(contributor) {
      //     typist.type(contributor.login).pause().delete();
      //   });
      // });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      activate();

      function activate() {

      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('gerbsDawg')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('gerbsDawg')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($scope, $firebaseArray, $log, $sanitize, $modal, hotkeys, $stateParams) {
    var vm = this;
    var firebase = null;
    vm.sayings = [];
    vm.addSaying = addSaying;
    vm.randomSaying = '';
    vm.registerHotkeys = registerHotkeys;
    vm.openModal = openModal;
    vm.modalIsOpen = null;

    activate();

    function activate() {
      firebase = new Firebase('https://gerbsdawg.firebaseio.com/');

      var data = $firebaseArray(firebase.child('sayings'))
      data.$loaded().then(function() {
        vm.sayings = data;
        // angular.forEach(vm.sayings, function(v, i) {
        //   // console.log('items', v);
        //   console.log('item index', v.index, i);
        //   v.update({ index: i });
        //   // if(v && !v.index) v.index = i;
        // });
      });

      vm.registerHotkeys();
      vm.modalIsOpen = false
      if($stateParams.openModal) vm.openModal();
      // seedData();
    }

    function addSaying(gerbMessage, dawgMessage) {
      vm.sayings.$add({
        gerbMessage: gerbMessage,
        dawgMessage: dawgMessage
      });
    }

    function openModal() {
      if(!vm.modalIsOpen) {
        var modalInstance = $modal.open({
          animation: true,
          templateUrl: 'app/addSayingModal/addSayingModal.html',
          controller: 'AddSayingModalController',
          controllerAs: 'vm',
          size: 'lg'
        });

        vm.modalIsOpen = true;

        modalInstance.result.then(function (selectedItem) {

        }, function () {
          vm.modalIsOpen = false;
        });
      }
    }

    function registerHotkeys() {
      hotkeys.add({
        combo: 'a',
        description: 'Add things',
        callback: function() {
          vm.openModal();
        }
      });
    }

    // function seedData() {
    //   vm.addSaying('Ask questions', 'Answers questions you haven\'t even thought to ask');
    // }
  }
})();

(function() {
  'use strict';

  angular
    .module('gerbsDawg')
    .controller('AddSayingModalController', AddSayingModalController);

  /** @ngInject */
  function AddSayingModalController($firebaseArray, $firebaseObject, $window, toastr, $modalInstance, $cookies, $rootScope, $interval, $q) {
    var vm = this;
    var firebase = null;
    var p;

    vm.sayings = [];
    vm.authenticate = authenticate;
    vm.addSaying = addSaying;
    vm.closeModal = closeModal;
    vm.isAuthenticated = $rootScope.isAuthenticated;
    vm.logout = logout;
    vm.demoteSaying = demoteSaying;
    vm.promoteSaying = promoteSaying;
    vm.newSaying = {};
    vm.password = '';
    vm.passwordSayings = [
      'Sorry, you gotta be The Big Dawg to edit this stuff',
      'Show me if you\'re yet another gerb or The Big Dawg',
      'You can\'t just talk like The Big Dawg, you have to walk like The Big Dawg',
      'Gerbs are always nosy about things that aren\'t their business',
      'What do think The Big Dawg would type down there?',
      'Is that you again, gerb?',
      'This area is reserved for non-gerb, Big Dawgs only',
      'Only a gerb can\'t get past this',
      'Don\'t gerb this up',
      'The Big Dawg knows this like the back of his paw'
    ];
    vm.passwordSayingIndex = 0;
    vm.randomizePasswordSaying = randomizePasswordSaying;
    vm.rememberPass = null;
    vm.removeSaying = removeSaying;
    vm.allowPromoteDemote = true;

    activate();

    function activate() {
      firebase = new Firebase('https://gerbsdawg.firebaseio.com/');
      vm.sayings = $firebaseArray(firebase.child('sayings'));

      vm.rememberPass = true;
      // Randomize the sayings
      randomizePasswordSaying();
      // Wait a satisfyingly long time to change it again if the user is still looking
      $interval(function() {
        randomizePasswordSaying();
      }, 10000);

      // Check if user is authenticated, if not, retrieve the password from Firebase
      if(!$rootScope.isAuthenticated) {
        // console.log('not authed');
        // Grab the password
        var obj = $firebaseObject(firebase);
        obj.$loaded().then(function() {
          p = obj.editPassword;

          // Check the cookie, if there's a value, use it to attempt to auth
          var cookie = $cookies.get('gerbdawg');
          if(cookie) {
            vm.password = cookie;
            authenticate();
          }
        });
      }
    }

    function authenticate() {
      // Check password show view and add cookie
      if(vm.password == p) {
        $rootScope.isAuthenticated = true;
        vm.isAuthenticated = true;
        // If the user wanted to remember the password..
        if(vm.rememberPass) {
          // Create a cookie with a 1-month expiry
          var now = new Date();
          $cookies.put('gerbdawg', p, {
            expires: new Date(now.getFullYear(), now.getMonth() + 1, now.getDate())
          });
        }
        toastr.success('You\'re in, Big Dawg!');
      }
      else {
        vm.password = '';
        toastr.error('Wrong password, gerb.');
        // If it's wrong, take out the cookie
        $cookies.remove('gerbdawg');
        randomizePasswordSaying();
      }
    }

    function addSaying() {
      vm.sayings.$add(vm.newSaying);
      vm.newSaying = {};
      toastr.success('New saying added!');
    }

    function closeModal() {
      $modalInstance.dismiss();
    }

    function logout() {
      $rootScope.isAuthenticated = false;;
      vm.isAuthenticated = false;
      $cookies.remove('gerbdawg');
    }

    function randomizePasswordSaying() {
      var currentIdx = vm.passwordSayingIndex;
      var newIdx = Math.floor(Math.random() * vm.passwordSayings.length);
      do  {
        newIdx = Math.floor(Math.random() * vm.passwordSayings.length);
      }
      while(currentIdx === newIdx);
      vm.passwordSayingIndex = newIdx;
    }

    function demoteSaying(saying) {
      if(saying.index > 0) {
        console.log('saying was ', saying.index);
        // var otherSaying = vm.sayings[saying.index - 1];
        var filtered = vm.sayings.filter(
          function(v) {
            return v.index === saying.index - 1;
          }
        );
        var otherSaying = filtered[0];
        otherSaying.index = saying.index--;
        console.log('saying is now ', saying.index);
        vm.allowPromoteDemote = false;
        $q.all([vm.sayings.$save(saying), vm.sayings.$save(otherSaying)]).then(function() {
          vm.allowPromoteDemote = true;
        });
      }
    }

    function promoteSaying(saying) {
      if(saying.index < vm.sayings.length - 1) {
        console.log('saying was ', saying.index);
        var filtered = vm.sayings.filter(
          function(v) {
            return v.index === saying.index + 1;
          }
        );
        // var otherSaying = vm.sayings[saying.index + 1];
        var otherSaying = filtered[0];
        otherSaying.index = saying.index++;
        console.log('saying is now ', saying.index);
        vm.allowPromoteDemote = false;
        $q.all([vm.sayings.$save(saying), vm.sayings.$save(otherSaying)]).then(function() {
          vm.allowPromoteDemote = true;
        });
      }
    }

    function removeSaying(saying) {
      if($window.confirm('Are you sure you want to remove this, Big Dawg?')) {
        vm.sayings.$remove(saying);
        toastr.error('Saying removed.');
      }
    }
  }
})();

